import React from 'react';
import { useForm, SubmitHandler } from 'react-hook-form';

import styles from './authentication-react-hook-form.module.scss';

type Inputs = {
  fullname: string;
  email: string;
  password: string;
};

type RegistrationEmailProps = {
  setVariant: (variant: 'default') => void;
};

const RegistrationEmail: React.FC<RegistrationEmailProps> = ({ setVariant }) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<Inputs>();

  const onSubmit = (data: any) => {
    alert(JSON.stringify(data));
  };

  const backForm = () => {
    setVariant('default');
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div className={styles.form__back} onClick={backForm}>
        {'<'} 
      </div>
      <input
        className={styles.form__input}
        {...register('fullname', {
          required: true,
          maxLength: 30,
          minLength: 3,
          pattern: /^[A-Za-z--]+$/i,
        })}
        placeholder="  "
        type="name"
      />
      <input
        className={styles.form__input}
        {...register('email', { required: true, maxLength: 30, minLength: 3 })}
        placeholder=""
        type="email"
      />
      {errors.fullname && <p className={styles.form__p}>     3 </p>}
      <input
        className={styles.form__input}
        {...register('password', { required: true, maxLength: 30, minLength: 9 })}
        placeholder=""
        type="password"
      />
      <input className={styles.form__input_submit} type="submit" placeholder="" />
    </form>
  );
};

export default RegistrationEmail;
